@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.main {
	width: 100vw;
	height: 100vh;
	background-color: black;
}

.main-logo {
	width: 100%;
	height: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.logo {
	width: 30px;
	height: 30px;
}

.logo-text {
	font-family: "Anton";
	font-size: 25px;
	color: white;
	margin-left: 5px;
}

.text {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: "Montserrat";
	font-size: 30px;
	font-weight: 500;
	color: white;
	text-align: center;
	padding-left: 16px;
	padding-right: 16px;
}

.version {
	font-family: "Montserrat";
	font-size: 16px;
	color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-left: 16px;
	padding-right: 16px;
}

.button-container {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.button {
    width: auto;
    height: 45px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #066bff;
    padding-left: 25px;
    padding-right: 25px;
}
.button:hover {
    cursor: pointer;
}
.button-text {
    font-family: "Montserrat";
    font-size: 12px;
    color: white;
    letter-spacing: -0.1;
}

.main-loader{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}
.main-loaderinner{
    width: 250px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.one,.two,.three {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: 5px;
    background-color: rgba(255,255,255,0.5);
}
.one{
    animation: loader 0.5s ease-in-out 0s infinite normal forwards;
}
.two{
    animation: loader 1s ease-in-out 0.25s infinite normal forwards;
}
.three{
    animation: loader 1s ease-in-out 0.75s infinite normal forwards;
}
@keyframes loader {
    0%{
        background-color:rgba(255,255,255,0.5);
    }
    50%{
        background-color: white;
    }
    /* 60%{
        background-color: aqua;
    } */
    100%{
        background-color: rgba(255,255,255,0.5);
    }
}